<template>
  <div class="video-player">
    <!-- ; ios  controls-->
    <video
      ref="video"
      v-if="showVideo"
      webkit-playsinline="true"
      playsinline="true"
      x-webkit-airplay="true"
      x5-video-player-type="h5"
      x5-video-player-fullscreen="true"
      x5-video-orientation="landscape"
      style="object-fit:fill"
      controlslist="nodownload"
      preload="auto"
      muted="true"
      :poster="poster"
      :src="src"
      @waiting="handleWaiting"
      @canplaythrough="state.isLoading = false"
      @playing="state.isLoading = false, state.controlBtnShow = false, state.playing=true"
      @stalled="state.isLoading = true"
      @error="handleError"
    >HTML5</video>
    <!--
  object-fit: fill   video
  poster:"img.jpg" 
  autoplay 
     auto - 
     meta - 
     none - 
 
  muted
  webkit-playsinline playsinline:    
  x5-video-player-type="h5" :  x5H5
  x5-video-player-fullscreen="true"  turefalse
  x5-video-orientation="portraint" landscape ,portraint
                                     portraintH5
                                     x5-video-player-typeH5
    -->
    <!-- Android,  -->
    <img v-show="!showVideo || state.isEnd" class="poster" :src="poster" alt />
    <!--  -->
    <div
      class="control"
      v-show="!state.isError"
      ref="control"
      @touchstart="touchEnterVideo"
      @touchend="touchLeaveVideo"
    >
      <!--  ||  || -->
      <div class="play" @touchstart.stop="clickPlayBtn" v-show="state.controlBtnShow">
        <img
          v-show="!state.playing && !state.isLoading"
          src="./../assets/video/content_btn_play.svg"
        />
        <img
          v-show="state.playing  && !state.isLoading"
          src="./../assets/video/content_btn_pause.svg"
        />
        <div class="loader" v-show="state.isLoading">
          <div class="loader-inner ball-clip-rotate">
            <div></div>
          </div>
        </div>
      </div>
      <!--  -->
      <!-- <div class="control-bar" v-show="state.controlBarShow"> -->
      <div class="control-bar" :style="{ visibility: state.controlBarShow ? 'visible' : 'hidden'}">
        <span class="time">{{video.displayTime}}</span>
        <span class="progress" ref="progress">
          <img
            class="progress-btn ignore"
            :style="{transform: `translate3d(${video.progress.current}px, 0, 0)`}"
            src="./../assets/video/content_ic_tutu.svg"
          />
          <span class="progress-loaded" :style="{ width: `${video.loaded}%`}"></span>
          <!--  -->
          <span
            class="progress-move"
            @touchmove.stop.prevent="moveIng($event)"
            @touchstart.stop="moveStart($event)"
            @touchend.stop="moveEnd($event)"
          ></span>
        </span>

        <span class="total-time">{{video.totalTime}}</span>
        <span class="full-screen" @click="fullScreen">
          <img src="./../assets/video/content_ic_increase.svg" alt />
        </span>
      </div>
    </div>
    <!--  -->
    <div class="error" v-show="state.isError">
      <p class="lose"></p>
      <p class="retry" @click="retry"></p>
    </div>
  </div>
</template>
<script>
import { throttle } from "@/utils/tool";

const pad = val => {
  val = Math.floor(val);
  if (val < 10) {
    return "0" + val;
  }
  return val + "";
};
const timeParse = sec => {
  let min = 0;
  min = Math.floor(sec / 60);
  sec = sec - min * 60;
  return pad(min) + ":" + pad(sec);
};

export default {
  name: "t-video",
  props: {
    src: {
      type: String,
      required: true
    },
    poster: {
      type: String,
      default: ""
    },
    showVideo: {
      // android
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      // video
      $video: null,
      // 
      player: {
        $player: null,
        pos: null
      },
      // progress
      progressBar: {
        $progress: null, // DOM
        pos: null
      },
      // video
      video: {
        loaded: 0, // 
        displayTime: "00:00", // 
        totalTime: "00:00", // 
        progress: {
          width: 0, // 
          current: 0 // 
        }
      },
      // 
      hideTimer: null,
      // 
      state: {
        controlBtnShow: true, // 
        controlBarShow: false, // 
        fullScreen: false,
        playing: false,
        isLoading: false,
        isEnd: false,
        isError: false
      },
      // 
      isFirstTouch: true,
      // 
      isPauseTouch: false
    };
  },

  methods: {
    init() {
      // video,video
      this.$video = this.$el.getElementsByTagName("video")[0];
      this.initPlayer();
    },
    // , video-player
    // getBoundingClientRect()client
    initPlayer() {
      const $player = this.$el;
      const $progress = this.$el.getElementsByClassName("progress")[0];
      // 
      this.player.$player = $player;
      this.progressBar.$progress = $progress;
      this.player.pos = $player.getBoundingClientRect();
      this.progressBar.pos = $progress.getBoundingClientRect();
      this.video.progress.width = Math.round(
        $progress.getBoundingClientRect().width
      );
    },
    //  & 
    clickPlayBtn() {
      if (this.state.isLoading) return;
      this.isFirstTouch = false;
      this.state.playing = !this.state.playing;
      this.state.isEnd = false;
      if (this.$video) {
        // 
        if (this.state.playing) {
          try {
            this.$video.play();
            this.$emit("selectChange");
            this.isPauseTouch = false;
            // 
            this.$video.addEventListener("progress", e => {
              this.getLoadTime();
            });
            // 
            this.$video.addEventListener(
              "timeupdate",
              throttle(this.getPlayTime, 100, 1)
            );
            // 
            this.$video.addEventListener("ended", e => {
              // 
              this.state.playing = false;
              this.state.isEnd = true;
              this.state.controlBtnShow = true;
              this.video.displayTime = "00:00";
              this.video.progress.current = 0;
              this.$video.currentTime = 0;
              this.src = this.src;
            });
          } catch (e) {
            // url
          }
        } else {
          // 
          this.isPauseTouch = true;
          this.$video.pause();
        }
      }
    },

    stopPlay() {
      // this.state.isEnd = true;
      // this.isFirstTouch = true;

      this.$video.load();
      this.state.playing = false;
      this.init();
    },
    // 
    touchEnterVideo() {
      if (this.isFirstTouch) return;
      if (this.hideTimer) {
        clearTimeout(this.hideTimer);
        this.hideTimer = null;
      }
      this.state.controlBtnShow = true;
      this.state.controlBarShow = true;
    },
    // 
    touchLeaveVideo() {
      if (this.isFirstTouch) return;
      if (this.hideTimer) {
        clearTimeout(this.hideTimer);
      }
      // , 
      if (this.isPauseTouch) {
        this.state.controlBtnShow = true;
        this.state.controlBarShow = true;
      } else {
        this.hideTimer = setTimeout(() => {
          this.state.controlBarShow = false;
          // loading
          if (this.state.isLoading) {
            this.state.controlBtnShow = true;
          } else {
            this.state.controlBtnShow = false;
          }
          this.hideTimer = null;
        }, 3000);
      }
    },
    // 
    handleWaiting() {
      this.state.controlBtnShow = true;
      this.state.isLoading = true;
    },
    // 
    handleError() {
      this.state.isError = true;
    },
    // 
    retry() {
      this.state.isError = false;
      this.init();
    },
    // 
    getPlayTime() {
      const percent = this.$video.currentTime / this.$video.duration;
      this.video.progress.current = Math.round(
        this.video.progress.width * percent
      );
      // 
      this.video.totalTime = timeParse(this.$video.duration);
      this.video.displayTime = timeParse(this.$video.currentTime);
    },
    // 
    getLoadTime() {
      // console.log('...',this.$video.buffered.end(0));
      this.video.loaded =
        (this.$video.buffered.end(0) / this.$video.duration) * 100;
    },
    // 
    moveStart(e) {},
    moveIng(e) {
      // console.log("...");
      let currentX = e.targetTouches[0].pageX;
      let offsetX = currentX - this.progressBar.pos.left;
      // 
      if (offsetX <= 0) {
        offsetX = 0;
      }
      if (offsetX >= this.video.progress.width) {
        offsetX = this.video.progress.width;
      }
      this.video.progress.current = offsetX;

      let percent = this.video.progress.current / this.video.progress.width;
      this.$video.duration && this.setPlayTime(percent, this.$video.duration);
    },
    moveEnd(e) {
      // console.log("...");
      let currentX = e.changedTouches[0].pageX;
      let offsetX = currentX - this.progressBar.pos.left;
      this.video.progress.current = offsetX;
      // offsetX
      let percent = offsetX / this.video.progress.width;
      this.$video.duration && this.setPlayTime(percent, this.$video.duration);
    },
    // 
    setPlayTime(percent, totalTime) {
      this.$video.currentTime = Math.floor(percent * totalTime);
    },
    // 
    fullScreen() {
      console.log("...");
      if (!this.state.fullScreen) {
        this.state.fullScreen = true;
        this.$video.webkitRequestFullScreen();
      } else {
        this.state.fullScreen = false;
        document.webkitCancelFullScreen();
      }
    }
  },
  mounted() {
    setTimeout(() => {
      this.init();
    }, 20);
  },
  beforeDestroy() {}
};
</script>
<style lang='less' scoped>
@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(180deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.loader {
  width: 58px;
  height: 58px;
  // background: rgba(15, 16, 17, 0.3);
  border-radius: 50%;
  position: relative;
  .ball-clip-rotate {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    > div {
      width: 15px;
      height: 15px;
      border-radius: 100%;
      margin: 2px;
      animation-fill-mode: both;

      border: 2px solid #fff;
      border-bottom-color: transparent;
      height: 26px;
      width: 26px;
      background: transparent !important;
      display: inline-block;
      animation: rotate 0.75s 0s linear infinite;
    }
  }
}

.video-player {
  // width: 100%;
  // height: 188px;
  width: 92vw;
  height: 33vh;
  overflow: hidden;
  position: relative;
  // 
  .poster {
    position: absolute;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
        background-color: #fff;
  }
  video {
    // border-radius: 10px 10px 0 0;
    width: 100%;
    height: 100%;
  }
  // 
  .control {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 2;
    left: 0;
    top: 0;
    background-color: transparent;
    .play {
      width: 58px;
      height: 58px;
      position: absolute;
      z-index: 3;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      img {
        width: 100%;
        height: 100%;
      }
    }
    &-bar {
      position: absolute;
      bottom: 8px;
      left: 50%;
      transform: translateX(-50%);
      height: 26px;
      display: flex;
      align-items: center;
      span {
        font-size: 12px;
        color: #fff;
        font-weight: 500;
        white-space: nowrap;
      }

      .progress {
        position: relative;
        margin: 0 8px;
        width: 203px;
        height: 2px;
        background: rgba(255, 255, 255, 0.4);
        border-radius: 2px;
        .progress-move {
          width: 100%;
          height: 26px;
          position: absolute;
          z-index: 100;
          left: 0;
          top: 0;
          transform: translateY(-50%);
          background-color: transparent;
        }
        .progress-btn {
          position: absolute;
          left: -10px;
          top: -8px;
          transition: all 16ms;
        }
        // 
        .ignore {
          width: 18px;
          height: 18px;
        }
        .progress-loaded {
          background-color: #fff;
          position: absolute;
          left: 0;
          top: 0;
          width: 0;
          height: 100%;
          border-radius: 2px;
          transition: all 16ms;
        }
      }
      .full-screen {
        width: 26px;
        height: 26px;
        margin-left: 8px;
        img {
          width: 100%;
          height: 100%;
        }
      }
    }
  }
  // 
  .error {
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.84);
    position: absolute;
    left: 0;
    top: 0;
    z-index: 2;
    .lose {
      text-align: center;
      font-size: 15px;
      font-weight: 500;
      color: rgba(255, 255, 255, 1);
      line-height: 23px;
      margin-top: 63px;
      margin-bottom: 40px;
    }
    .retry {
      margin: 0 auto;
      text-align: center;
      line-height: 38px;
      width: 101px;
      height: 38px;
      background: rgba(255, 255, 255, 1);
      border-radius: 19px;
      font-size: 15px;
      color: #008eff;
    }
  }
}
</style>


